.Asset {
  /* text-align: center; */
  /* background-color: #f5f6f7; */
  align-items: center;
  font-size: calc(15px + 4vmin);
  color: rgb(0, 0, 0);
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  font-family: "Josefin Sans", sans-serif;
  margin: 0 10px 0 10px;
}

.Loading {
  text-align: center; 
  /* background-color: #f5f6f7; */
  align-items: center;
  font-size: calc(15px + 4vmin);
  color: rgb(0, 0, 0);
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
