}

.MessageStripe {
  margin: 50px 10px 10px 10px;

  /* padding: 0 16px 0 16px; */
}
.square {
  position: relative;
  width: 50%;
}

.square:after {
  content: "";
  display: block;
  padding-bottom: 100%;
}

.AssetCard {
  margin-bottom: 10px;
}

.TransactionCard {
  margin-top: 0px;
}
.TimeLine {
  text-align: center;
}

.content {
  position: absolute;
  width: 100%;
  height: 100%;
}

